.wildfire{font-family:'Helvetica Neue',arial,sans-serif;font-size:15px;max-width:39rem;margin:0 auto;padding:10px}.wildfire img.anonymous{opacity:.9;-webkit-filter:grayscale(70%);filter:grayscale(70%)}.wildfire .wf-comment-content pre{overflow:auto;max-width:calc(-41rem);max-height:300px;padding:10px 20px;background:#f6f8fa}.wildfire .wf-comment-content img{max-width:100%!important}.wildfire .code-overflow-hidden pre{overflow:hidden!important}.wildfire .thumbnail{line-height:150px;display:inline-block;overflow:hidden;width:150px;height:150px;cursor:pointer;border:1px solid #fff;border-radius:4px;background:0 0;background-repeat:no-repeat;background-position:50%;background-size:100%;box-shadow:0 1px 3px rgba(0,0,0,.2)}.wildfire .top-reply-area{display:flex;flex-direction:row;margin-left:60px;justify-content:space-between}.wildfire .top-reply-area div{font-size:10px;display:flex}.wildfire .top-reply-area .tool-bar{margin-right:10px;flex:1}.wildfire .ivu-spin-text div{display:inline-block}.wildfire .ivu-switch-checked .ivu-switch-inner{font-size:8px;left:4px}.wildfire .ivu-switch-inner{font-size:8px;left:12.5px}.wildfire .ivu-modal-confirm-body{overflow:hidden;max-height:400px;text-align:center}.wildfire .vertical-center-modal{display:flex;align-items:center;justify-content:center}.wildfire .vertical-center-modal .ivu-modal{top:0}.wildfire .ivu-spin{position:unset;background-color:unset}.wildfire .spin-icon{animation:ani-demo-spin 1s linear infinite}@keyframes ani-demo-spin{from{transform:rotate(0)}50%{transform:rotate(180deg)}to{transform:rotate(360deg)}}


